
#nav {
  position: absolute;
  bottom: 0;
  left: 0;
  box-sizing: border-box;
  width: 100%;
  height: 3.2rem;
  z-index: 9;
  font-size: 0.53333rem;
  background: #fff;
  border-top: 1px solid #dddddd;
}
#nav ul {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    -moz-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    -webkit-box-pack: center;
    -moz-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
}
#nav ul li {
      flex: 1;
      text-align: center;
      color: #202020;
}
#nav ul li em {
        display: block;
        width: 1.17333rem;
        height: 1.06667rem;
        margin: 0.53333rem auto 0.26667rem;
}
#nav ul li strong {
        font-weight: 200;
}
#nav ul li:nth-child(1) em {
      background: url("../images/1_1.png") center no-repeat;
      background-size: contain;
}
#nav ul li:nth-child(2) em {
      background: url("../images/2_2.png") center no-repeat;
      background-size: contain;
}
#nav ul li:nth-child(3) em {
      background: url("../images/3_3.png") center no-repeat;
      background-size: contain;
}
#nav ul li:nth-child(1).active {
      color: #1055f1;
}
#nav ul li:nth-child(1).active em {
        background: url("../images/1.png") center no-repeat;
        background-size: contain;
}
#nav ul li:nth-child(2).active {
      color: #1055f1;
}
#nav ul li:nth-child(2).active em {
        background: url("../images/2.png") center no-repeat;
        background-size: contain;
}
#nav ul li:nth-child(3).active {
      color: #1055f1;
}
#nav ul li:nth-child(3).active em {
        background: url("../images/3.png") center no-repeat;
        background-size: contain;
}



